import { HomeRounded, FaceRounded, TextSnippetRounded, TerminalRounded, MessageRounded, ConnectWithoutContactRounded } from '@mui/icons-material'

const tabProps = (index) => {
  return {
    id: `sidebar-tab-${index}`,
    'aria-controls': `tabpanel-${index}`,
  }
}

export const tabsData = () => {
  const tabs = [
    { label: ' ', icon: <HomeRounded />, ...tabProps(0) },
    { label: ' ', icon: <FaceRounded />, ...tabProps(1) },
    { label: ' ', icon: <TextSnippetRounded />, ...tabProps(2) },
    { label: ' ', icon: <TerminalRounded />, ...tabProps(3) },
    { label: ' ', icon: <MessageRounded />, ...tabProps(4) },
    { label: '  ', icon: <ConnectWithoutContactRounded />, ...tabProps(5) },
  ]
  return tabs
}
